@font-face {
  font-family: "TT_Commons";
  src: url("../font/TT_Commons_Regular.otf") format("opentype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Vogun";
  src: url("../font/vogun.otf") format("opentype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
html,
body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 1.5em;
  font-weight: bold;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.8em;
}

h3 {
  font-size: 1.6em;
}

h4 {
  font-size: 1.4em;
}

h5 {
  font-size: 1.2em;
}

h6 {
  font-size: 1em;
}

ol,
ul {
  margin-bottom: 1.5em;
  padding-left: 1.5em;
}

table {
  width: 100%;
  margin-bottom: 1.5em;
  border-spacing: 0;
  border-collapse: collapse;
}

table th,
table td {
  padding: 0.5em;
  border: 1px solid #000;
}

table th {
  font-weight: bold;
}

caption,
table th,
table td {
  font-weight: normal;
  text-align: left;
  vertical-align: middle;
}

q,
blockquote {
  quotes: none;
}

q::before,
q::after,
blockquote::before,
blockquote::after {
  content: none;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body * {
  max-height: 99999em;
  word-break: break-word;
}

p {
  margin-bottom: 1.5em;
}

a {
  color: inherit;
}

a:hover {
  text-decoration: none;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

a img {
  border: none;
}

input,
label,
select,
button {
  line-height: inherit;
}

input,
label,
select,
button,
textarea {
  display: inline-block;
  margin: 0;
  padding: 0;
  border: 0;
  background: none;
  font: inherit;
  word-break: normal;
  vertical-align: middle;
  white-space: normal;
  -webkit-appearance: none;
}

button {
  color: inherit;
}

textarea {
  overflow: auto;
  line-height: 1.5;
  white-space: pre-line;
  resize: none;
}

button:focus {
  outline: 0;
}

input:focus,
textarea:focus,
select:focus {
  outline: auto;
}

input[type=search] {
  -webkit-appearance: textfield;
}

::-webkit-search-decoration {
  display: none;
}

@media print {
  /* Remove all backgrounds and enforce black text */
  * {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  /* Remove useless UI */
  nav,
  aside,
  header,
  footer,
  form,
  button,
  input,
  textarea,
  iframe,
  audio,
  video {
    display: none !important;
  }
  /* Reset links to show URLs */
  a {
    color: #000 !important;
    text-decoration: underline !important;
  }
  a[href]::after {
    content: " (" attr(href) ")";
    font-size: 90%;
  }
  /* Images only if needed */
  img {
    max-width: 100% !important;
    page-break-inside: avoid;
  }
  /* Tables */
  table {
    border-collapse: collapse !important;
  }
  th,
  td {
    border: 1px solid #000 !important;
    padding: 4px;
  }
  /* Typography adjustments */
  body {
    margin: 0;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    page-break-after: avoid;
    font-weight: bold !important;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  h2,
  h3 {
    page-break-before: avoid;
  }
  /* Avoid cutting important blocks */
  blockquote,
  pre {
    page-break-inside: avoid;
  }
  /* Reset page margins (browser default often adds its own) */
  @page {
    margin: 0;
    size: 11in 14.028in;
  }
  @page :left {
    margin: 0;
  }
  @page :right {
    margin: 0;
  }
  @page :first {
    margin-top: 0;
  }
}
@media only screen and (max-width: 767.98px) {
  .pc {
    display: none !important;
  }
}

@media (min-width: 768px) {
  .sp {
    display: none !important;
  }
}

@media only screen and (max-width: 1279.98px) {
  .pc-layout {
    display: none !important;
  }
}

@media (min-width: 1280px) {
  .sp-layout {
    display: none !important;
  }
}

.trans {
  transition: opacity 0.5s ease;
  cursor: pointer;
}

.trans:hover {
  opacity: 0.8;
}

.hidden {
  display: none;
}

.image-common {
  width: 100%;
  height: auto;
  display: block;
}

.object-common {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

.contain-common {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

html {
  -webkit-font-smoothing: antialiased;
  line-height: 1;
  -webkit-text-size-adjust: none;
  -moz-text-size-adjust: none;
  -ms-text-size-adjust: none;
}
@media (min-width: 768px) {
  html {
    font-size: 5.2083333333dvw;
  }
}
@media only screen and (max-width: 767.98px) {
  html {
    font-size: 26.6666666667dvw;
  }
}

a {
  text-decoration: none;
}

img,
svg {
  image-rendering: smooth;
  shape-rendering: geometricprecision;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input,
textarea,
select {
  display: block;
  font: inherit;
  color: inherit;
}

body {
  color: #195450;
  font-family: "TT_Commons", serif;
  font-size: 0.2rem;
  font-weight: 400;
  line-height: 1.3;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

.main {
  width: 100%;
  height: 100%;
  position: relative;
}
.wrapper {
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .wrapper {
    width: calc(100% - 0.6rem);
    max-width: 16.22rem;
  }
}
@media only screen and (max-width: 767.98px) {
  .wrapper {
    width: calc(100% - 0.24rem);
  }
}

.title-common-primary {
  text-align: center;
  color: #fff;
  font-weight: 500;
  text-transform: uppercase;
  font-family: "Vogun", sans-serif;
  position: relative;
  z-index: 1;
  width: 100%;
}
.title-common-primary.is-lion {
  color: #c19e74;
}
.title-common-primary.is-start {
  text-align: left;
}
@media (min-width: 768px) {
  .title-common-primary {
    margin-bottom: 0.16rem;
    font-size: 0.5rem;
    line-height: 1.2;
  }
}

.button-common-primary {
  position: relative;
  background-color: #c19e74;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  border-radius: 0.5rem;
}
.button-common-primary::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  margin-right: 0.2rem;
  mask: url(../img/common/icon_arrow_01.svg) 50% 50%/contain no-repeat;
  transition: margin-right 0.5s ease-in-out;
  width: 0.1rem;
  height: 0.1rem;
  background-color: #fff;
}
@media (min-width: 768px) {
  .button-common-primary {
    padding: 0.02rem 0.35rem 0.03rem 0.1rem;
    width: 1.66rem;
    min-height: 0.45rem;
  }
}